<template>
	<div id="viewport">
		<div id="info">
			<h1>Level {{ level }}</h1>
		</div>
		<div id="sprite">
			<lottie :options="defaultOptions" :height="400" :width="400" v-on:animCreated="handleAnimation" />
		</div>
		<div id="input" class="hide">
			<b-form inline>
	      <b-input d="inputField" placeholder="Saisir votre parcours" v-model="stringInput" />
	      <b-button variant="primary" v-on:click="startMove" :disabled="this.cantTest">Go</b-button>
    	</b-form>
	  	<p 
	  		v-for="(input, inputKey) in userInput" 
	  		:id="['input_' + inputKey]"
	  		class="input"
	  	>
	  	{{ input }}
	  	</p>
	  </div>
	  <div id="rooms" :style="{ width: roomsWidth + 'px', left: startOffset + 'px'}">
	  	<div 
	  		v-for="(room, roomKey) in rooms" 
	  		:id="['room_' + roomKey]"
	  		class="room"
	  		:style="{ left: room.position + 'px'}"
	  	>
	  	<p>{{ room.d }}</p>
	  	</div>
	  </div>
	  <div id="bottomMenu">
		  <b-button id="start" :disabled="this.inProgress" variant="primary" v-on:click="startIntro">Démarrer</b-button>
		</div>
	</div>
</template>

<script>

import Velocity from 'velocity-animate';
import Lottie from './lottie.vue';
import * as animationData from '../assets/anim/run.json';

export default {
  name: 'Main',
  components: {
     'lottie': Lottie
  },
  data () {
    return {
    	stringInput: '',
    	levels: [],
      rooms: [],
      userInput: [],
      roomWidth: 400,
      roomsWidth: 100000,
      roomCount: 0,
      inProgress: false,
      cantTest: true,
      defaultOptions: {animationData: animationData, autoplay: false},
      animationSpeed: 1,
      level:1,
      roomNumber:4,
      totalLevel:10,
      startOffset:0
    }
  },
  watch: {
    // whenever stringInput changes, this function will run
    stringInput: function () {
      this.userInput = this.stringInput.split('');
      this.cantTest = !(this.userInput.length == this.rooms.length);
    }
  },
  methods: {
  	reset:function() {
  		$(".room").removeClass('ok');
  		$(".room").removeClass('no');
  		this.userInput = [];
  		this.stringInput = '';
  		this.roomCount = 0;
  		$('#input').addClass('hide');
  	},
  	win:function() {
  		if(this.level < this.totalLevel) {
  			this.level++;
  			this.rooms = this.levels[this.level - 1];
  			this.defineRoomPosition();
  			this.startOffset = -this.rooms[this.rooms.length - 1].position;
  			$('#sprite').removeClass('ready');
  			$('#input').addClass('hide');
  		}
  	},
  	handleAnimation: function (anim) {
      this.anim = anim;
    },
  	init: function() {
  		var scope = this;
  		this.rooms = this.levels[this.level - 1];
  		this.defineRoomPosition();
  		this.startOffset = -this.rooms[this.rooms.length - 1].position;
  	},
  	startIntro: function() {
  		$('#sprite').removeClass('ready');
  		if(this.userInput) {
  			this.reset();
  		}
  		var scope = this;
  		this.inProgress = true;
  		$('#start').css('display', 'none');
  		$("#rooms").velocity({ left: this.roomWidth+'px' }, 
  		{ 
  			duration: 5000,
        easing:'easeInOutSine',
        complete: function() {
        	scope.startTyping();
        },
  		});
  	},
  	startTyping: function() {
  		$('#input').removeClass('hide');
  		$('#sprite').addClass('ready');
  		this.inProgress = false;
  	},
  	defineRoomPosition: function() {
  		var totalPosition = 0;
  		for (var i = 0; i < this.rooms.length; i++) {
  			totalPosition += this.rooms[i].gap;
  			this.rooms[i].position = totalPosition;
  			this.rooms[i].id = i;
  			totalPosition += this.roomWidth;
  		};
  		//update rooms width
  		this.roomsWidth = totalPosition+1000;
  	},
  	startMove: function() {
  		var scope = this;
  		this.inProgress = this.cantTest = true;
			this.move();
			this.anim.play();
  	},
  	move: function() {

  		if(this.roomCount < this.rooms.length)
  		{
  			this.moveToRoom(this.rooms[this.roomCount]);
  			this.roomCount++;
  		}
  		else {
  			//end session
  			this.endMove();
  			this.win();
  		}
  	},
  	moveToRoom:function(room) {
  		var scope = this;
  		//to do: resolve issue with scroll hidden
  		var offset = room.position;
  		$("#rooms").velocity({ left: -offset+'px' }, 
  		{ 
  			duration: 1000 + room.gap,
        complete: function() {
        	scope.checkRoom(room);
        },
        easing:'linear',
  		});
  		//method with scroll
			/*$("#rooms").velocity('scroll', {
			 	offset: offset+'px',
  			duration: 2000+room.gap,
        complete: function() {
        	scope.checkRoom(room);
        },
        container:$('#viewport'),
        easing:'linear',
        axis: "x" 
  		});*/
  	},
  	checkRoom: function(room) {
  		var scope = this;
  		if(room.d == this.userInput[room.id]) {
  			$('#room_'+room.id).addClass('ok');
  			$('#input_'+room.id).addClass('ok');
  			scope.move();
  		}
  		else {
  			$('#room_'+room.id).addClass('no');
  			$('#input_'+room.id).addClass('no');
  			scope.endMove();
  		}
  		/*setTimeout(function() {
  			scope.move();
  		}, 1000);*/
  		
  	},
  	endMove: function() {
  		this.inProgress = false;
  		$('#start').css('display', 'block');
  		this.anim.pause();
  	},
  	generateLevels: function() {
  		for (var i = 1; i < this.totalLevel + 1; i++) {
  			var level = [];
  			for (var j = 0; j < this.roomNumber; j++) {
  				var d = Math.round(Math.random() * 3);
  				var gap = Math.round(Math.random() * 300);
  				var room = {d: d, gap: gap};
  				level.push(room);
  			};
  			this.levels.push(level);
  			this.roomNumber++;
  		};
  	}
  },
  created: function() {
  	this.generateLevels();
  	this.init();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

	$roomWidth: 400px;

	#viewport {
		position: absolute;
		top: 0; left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
	}

	#info {
		position: absolute;
		top: 10px;
		left: 0px;
		width: 100%;
		h1 {
			width: 100%;
			text-align: center;
		}
	}

	#roomsContainer {
		position: absolute;
		top: 0; left: 0;
		width: 100%;
		height: 100%;
	}

	#rooms, #sprite {
		top: 50%;
		margin-top: -200px;
	}

	#rooms {
		position: absolute;
		left: $roomWidth;
		margin-right: 1000px;
	}

	#sprite {
		position: absolute;
		left: 0;
		z-index: 100;
		//border: 1px solid;
		width: $roomWidth;
		height: $roomWidth;
		opacity: 0;
		transition: opacity .5s ease-in-out;
	}

	#sprite.ready {
		display:block;
		opacity: 1;
	}

	#start {
		position: fixed;
		bottom: 40px;
		left: 50%;
		transform: translateX(-50%);
	}

	.guy {
		position: relative;
		height: 60%;
		width: 10%;
		margin-top: 40%;
		background: black;
		margin-left: 20px;
	}

	#input {
		position: fixed;
		top: 60px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 9999;
	}

	.hide {
		display: none;
	}

	.input {
		display: inline-block;
	}
	.input.ok {
		color: green;
	}
	.input.no {
		color: red;
	}

	.room {
		width: $roomWidth;
		height: $roomWidth;
		background: #EEE;
		border: 1px solid #000;
		position: absolute;
		p {
			font-size: 30px;
			text-align: center;
			width: 100%;
		}
	}

	.room.ok {
		background: green;
	}

	.room.no {
		background: red;
	}
</style>



